<template>
  <div class="home">
    <!-- ÂÜÖÂÆπ -->
    <div class="wrap-top">
      <van-nav-bar :border="false" left-arrow @click-left="onClickLeft" />
      <div class="tip-title" v-if="recordStatus === 1 || recordStatus === 3">ËØ∑Èù¢ÂêëÂ±èÂπïÂºÄÂßãÂΩïÂà∂ËßÜÈ¢ë</div>
      <div class="tip-title" v-if="recordStatus === 2">ËØ∑Èù¢ÂêëÂ±èÂπï {{ titles }}</div>
      <div class="tip-title" v-if="recordStatus === 4">Ê≠£Âú®Ê£ÄÊµã‰∏≠...</div>

      <!-- ‰∫∫ÂÉèÈÉ®ÂàÜ -->
      <cw-video :showDefaultImg="!isOpenVideo" :loading="loading" ref="cwVideoRef">
        <template #imgSlot>
          <div class="video-wrap" id="video-box" v-show="isOpenVideo">
            <video id="videoPlay" playinline autoplay></video>
          </div>
        </template>
      </cw-video>
    </div>
    <div class="wrap-bottom">
      <!-- ÊèêÁ§∫‰ø°ÊÅØ -->
      <TipInfo v-if="isShowInfo" />
      <!-- ÂäüËÉΩÊåâÈíÆ -->
      <div class="op-btn" v-if="isShowBtn">
        <CwButton @click="openCamera" :loading="btnLoading">ÂºÄÂßãÂΩïÂà∂ËßÜÈ¢ë</CwButton>
      </div>
    </div>

    <!-- ÂºπÂá∫Èù¢Êùø -->
    <CwNativeCameraPopup v-model="showNativeCamera" :titles="titles" @nativeRecord="nativeRecord" @cancelClick="nativeCancelClick" />
    <CwRsultPopup v-model="showResult" @reRecord="reRecord" @cancelClick="cancelClick" />
  </div>
</template>

<script>
import CwButton from '@/components/cw-button/index.vue'
import CwVideo from '@/components/cw-video/index.vue'
import CwNativeCameraPopup from '@/components/cw-native-camera-popup/index.vue'
import CwRsultPopup from '@/components/cw-result-popup/index.vue'
import axios from 'axios'
import TipInfo from './components/tip-info.vue'
const URL = window.config.uploadUrl
const ACTION_NUM = window.config.actionNum || 2 // Âä®‰Ωú‰∏™Êï∞
export default {
  components: {
    CwButton,
    CwVideo,
    CwNativeCameraPopup,
    CwRsultPopup,
    TipInfo
  },
  data() {
    return {
      showNativeCamera: false,
      showResult: false,
      //  cwVideoRef: false,
      videoInstance: null,
      isSupport: true,
      btnLoading: false,
      recordStatus: 1,
      activeActions: [],
      isUploadFinish: false,
      actionsArr: [
        { title: 'Áú®Áú®Áúº', action: '1' },
        { title: 'Âº†Âº†Âò¥', action: '2' },
        { title: 'ÊëáÊëáÂ§¥', action: '5' }
      ]
    }
  },
  computed: {
    isOpenVideo() {
      return this.isSupport && this.recordStatus === 2
    },
    isShowInfo() {
      return this.recordStatus === 1 || this.recordStatus === 3
    },
    isShowBtn() {
      return this.recordStatus === 1 || this.recordStatus === 3
    },
    loading() {
      return this.recordStatus === 4 && !this.isUploadFinish
    },
    titles() {
      const titlesArr = this.activeActions.map(ele => ele.title)
      return titlesArr.join('„ÄÅ')
    },
    actions() {
      const actionsArr = this.activeActions.map(ele => ele.action)
      return actionsArr.join(',')
    }
  },
  mounted() {
    this.initFs()
    this.initVideo()
  },
  methods: {
    // ÂàùÂßãÂåñÂä®‰Ωú
    initFs() {
      if (ACTION_NUM === 1) {
        const num = Math.floor(Math.random() * (this.actionsArr.length - 1) + 1)
        this.activeActions = this.actionsArr.filter((ele, index) => index === num - 1)
      } else if (ACTION_NUM === 2) {
        const num = Math.floor(Math.random() * (this.actionsArr.length - 1) + 1)
        this.activeActions = this.actionsArr.filter((ele, index) => index !== num - 1)
      } else {
        this.activeActions = [...this.actionsArr]
      }
    },

    // ÊâìÂºÄcameara
    openCamera() {
      const self = this
      if (self.isSupport) {
        self.btnLoading = true
        // ÊîØÊåÅÈ°µÈù¢ÂÜÖÂΩïÂà∂
        // ÊâìÂºÄÊëÑÂÉèÂ§¥
        self.videoInstance.openCamera({
          onError() {
            console.warn('‰∏çÊîØÊåÅ')
            self.showNativeCamera = true
            self.isSupport = false
            self.recordStatus = 3
            self.btnLoading = false
          },
          onSuccess() {
            console.warn('ÊîØÊåÅ')
            self.recordStatus = 2
            self.startCollect()
            self.btnLoading = false
          }
        })
      } else {
        // ‰∏çÊîØÊåÅÈ°µÈù¢ÂÜÖÂΩïÂà∂
        self.showNativeCamera = true
      }
    },

    // ÂºÄÂßãÂΩïÂà∂
    startCollect() {
      const recordTime = (window.config.recordTime || 5000) / 1000
      let isFirst = true
      const self = this
      self.videoInstance.startCollect({
        time: recordTime, // ÂèØ‰∏çËÆæÁΩÆ, ‰∏çËÆæÁΩÆÈúÄË¶ÅÊâãÂä®ÊéßÂà∂ÁªìÊùü
        timeCallback(clock) {
          if (isFirst) {
            isFirst = false
            self.$refs.cwVideoRef.start(recordTime)
          }
          console.warn(clock)
          if (clock <= 0) {
            self.recordStatus = 4
          }
        }
      })
    },

    // ÂàùÂßãÂåñÊëÑÂÉèÂ§¥
    initVideo() {
      this.recordStatus = 1
      const self = this
      // ÂΩïÂà∂ËßÜÈ¢ëÂàùÂßãÂåñ
      this.videoInstance = new CWMediaCollector({
        webmWasmPath: './static/webm-wasm.wasm', // ‰æùËµñÊñá‰ª∂
        webmWorkerPath: './static/webm-worker.js', // ‰æùËµñÊñá‰ª∂webm-worker.jsÁöÑÊú¨Âú∞Ë∑ØÂæÑÔºå‰∏ç‰º†ÂèÇÊï∞ÂàôÈªòËÆ§Ë∞ÉÁî®ÊúçÂä°Á´ØËé∑ÂèñÊñá‰ª∂
        el: document.getElementById('video-box'), // document.getElementById('video-box'), // ËßÜÈ¢ëÊòæÁ§∫‰ΩçÁΩÆ, ËßÜÈ¢ëÁ™óÂè£Ê†πÊçÆelÂ§ßÂ∞èÈÄÇÈÖç
        height: 480,
        width: 480,
        autoOpenNativeCamera: false, // Â§±Ë¥•ÁöÑÊó∂ÂÄô‰∏çËá™Âä®ÊâìÂºÄÊëÑÂÉèÂ§¥
        mode: 1, // mode, // Ê®°Âºè„Äê1ÔºöÂÖºÂÆπÊÄß‰ºòÂÖàÔºõ2Ôºö‰ΩìÈ™å‰ºòÂÖàÔºõ3ÔºöÂéüÁîüÁõ∏Êú∫ÂΩïÂà∂ËßÜÈ¢ë„Äë
        maxFileSize: 10, // 10, // ÈùûÂøÖ‰º†ÔºåÈôêÂà∂ÊúÄÂ§ßÊñá‰ª∂(M)
        frameRate: 10,
        isFrontCamera: true,
        mimeType: 'video/webm;codecs=h264',
        onInitSuccess(res) {
          if (res.code === 20002) {
            self.isSupport = false
            // ÂºπÁ™óËß¶Âèë
          } else {
            self.isSupport = true
          }
        },
        onSuccess: function (res) {
          // ÂΩïÂà∂ÁªìÊùüÂõûË∞ÉÂáΩÊï∞
          console.log('recordingEnd==', res)
          if (res.code === 20000) {
            console.log('ÂΩïÂà∂ÁªìÊùü‰∫Ü‚Äî‚Äî‚Äî‚Äî‚Äî‚Äî‚Äî‚Äî')
            if (res.data) {
              self.postBlob(res.data)
              console.warn('ÂΩïÂèñÁöÑÊï∞ÊçÆ', res.data)
              self.recordStatus = 4
              self.showNativeCamera = false
            } else {
              console.warn('ÂΩïÂà∂ÁöÑÊï∞ÊçÆ‰∏∫0')
              self.showResult = true
            }
          } else {
            // ÂΩïÂà∂Êú™ÊàêÂäü
            self.showResult = true
          }
        },
        onError: function (res) {
          console.log('üöÄ ~ html ~ initVideo ~ res', res, res.code, '--', res.message)
          console.log(res.message)
          // ÂΩïÂà∂ËøáÁ®ã‰∏≠Âá∫Èîô
          self.showResult = true
        }
      })
    },
    // Â≠òÂÇ®ÁªìÊûú
    setResult(result) {
      localStorage.setItem('result', JSON.stringify(result))
    },
    // ËßÜÈ¢ë‰º†Ëæì
    postBlob(blob) {
      const self = this
      CWMediaCollector.blobToBase64(blob, function (base64Data) {
        console.warn('ÂΩïÂà∂ÁöÑÊñá‰ª∂', base64Data)
        console.warn('Êñá‰ª∂Â§ßÂ∞èByte', base64Data.length)
        self.checkFace(base64Data, function (res) {
          console.log(res)
          if (res.isAlive && res.image) {
            self.setResult({
              result: true,
              size: base64Data.length,
              image: 'data:image/jpeg;base64,' + res.image,
              res
            })
            self.$router.push({
              path: '/result'
            })
          } else if (!res.isAlive && res.result === 0) {
            self.showResult = true
          } else if (!res.isAlive && res.result !== 0) {
            self.showResult = true
          }
        })
      })
    },
    // Ë∞ÉÁî®Êé•Âè£Ê£ÄÊµãface
    checkFace(videoData, callback) {
      // Ë∞ÉÁî®Ê£ÄÊµãÊé•Âè£
      const data = {
        ...(window.config.uploadParameter || {}),
        video: videoData,
        action: this.actions
      }
      this.isUploadFinish = false
      axios
        .post(URL, data)
        .then(res => {
          if (res.status === 200) {
            callback(res.data)
          } else {
            console.warn('Â§±Ë¥•status !== 200:', res.status)
          }
        })
        .catch(err => {
          console.warn('ËØ∑Ê±ÇÂá∫Èîô:', err)
        })
        .finally(() => {
          this.isUploadFinish = true
        })
    },
    // ÂÜçÊ¨°ÂΩïÂà∂ËßÜÈ¢ë
    reRecord() {
      console.warn('ÈáçÊñ∞ÂΩïÂà∂ÂäüËÉΩ')
      this.showResult = false
      this.$refs.cwVideoRef.init() // ÂàùÂßãÂåñËøõÂ∫¶
      this.initFs()
      this.initVideo()
    },
    // ÂéüÁîüËßÜÈ¢ëÂΩïÂà∂
    nativeRecord() {
      this.videoInstance && this.videoInstance.openNativeCamera()
    },
    // ÁÇπÂáª‰∫ÜËøîÂõûÂäüËÉΩ
    onClickLeft() {
      console.warn('ÁÇπÂáª‰∫ÜËøîÂõûÈîÆÁõò')
      const url = window.config.returnUrl
      location.href = url
      // ÂõûË∞ÉÂú∞ÂùÄ
    },

    // Â§±Ë¥•ÁöÑÊó∂ÂÄôÈÄÄÂá∫
    cancelClick() {
      console.warn('ÁÇπÂáª‰∫ÜÂ§±Ë¥•ÁöÑÈÄÄÂá∫ÂäüËÉΩ')
      // ÈÄÄÂá∫ÂõûË∞É
      const url = window.config.failedUrl
      location.href = url
    },
    // ÂéüÁîüÁõ∏Êú∫ÁöÑÈÄÄÂá∫ÂäüËÉΩ
    nativeCancelClick() {
      const url = window.config.returnUrl
      location.href = url
    }
  }
}
</script>

<style lang="less" scoped>
.home {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
  .op-btn {
    padding: 0 28px;
  }
  .tip-title {
    font-size: 36px;
    color: #333742;
    text-align: center;
    line-height: 36px;
    font-weight: 400;
    padding-top: 120px;
    padding-bottom: 60px;
  }
  .wrap-bottom {
    padding-bottom: 120px;
    padding-top: 60px;
  }
}
</style>
